.state-square{
    width: 280px;
    height: 150px;
    display: inline-block;
    margin: 20px;
    background-color: aquamarine;
    text-align: left;
    padding: 20px;
    position: relative;
}

@media only screen and (max-width: 600px) {
    body {
      background-color: lightblue;
    }
  }

.state-name{
    font-weight: 700;
}

.favorite{
    height: 25px;
    width: 25px;
    position: absolute;
    bottom: 10px;
    right: 10px;
}